.widget {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search post type

    &.qodef-search-post-type-widget {
        .qodef-post-type-search-field {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 3px;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            float: left;
            font-family: $default-text-font;
            font-size: 12px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            outline: 0;
            padding: 0 25px 0 20px;
            position: relative;
            vertical-align: top;
            width: 100%; /* width of loading icon */

            @include qodefPlaceholder {
                color: $widget-text-color;
            }

            transition: color .2s ease-in-out;
            -webkit-appearance: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent posts

    &.qodef-recent-post-widget {
        .qodef-recent-posts {
            .qodef-rp-item {
                margin: 0 0 10px;
                padding: $widget-list-item-padding;

                a {
                    display: table;
                    width: 100%;

                    .qodef-rp-image,
                    .qodef-rp-title {
                        display: table-cell;
                        vertical-align: middle;
                    }

                    .qodef-rp-title {
                        transition: color .2s ease-in-out;
                        font-weight: 600;
                    }

                    .qodef-rp-image {
                        height: 56px;
                        padding-right: 20px;
                        width: 56px;

                        img {
                            display: block;
                        }
                    }

                    &:hover {
                        .qodef-rp-title {
                            color: $default-text-color;
                        }
                    }
                }
            }
        }
    }

    &.qodef-blog-list-widget {
        .qodef-bl-minimal {

            .qodef-bli-content {
                display: flex;
                flex-direction: column-reverse;
                padding-right: 57%;
                box-sizing: border-box;
            }
            .qodef-post-info-date{
                margin: 13px 0 0;

                a {
                    color: $default-text-color;
                }
            }

            .qodef-post-title {
                a {
                    color: $default-heading-color;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
}