@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.qodef-comment-holder {
    @include qodefRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .qodef-comment-holder-inner {
        padding: 85px 0 20px;
    }

    .qodef-comment {

        &.qodef-pingback-comment {

            .qodef-comment-text {
                padding: 0;
            }
        }
    }

    .qodef-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include qodefRelativeHolderLayout();
            margin: 9px 0 0;
            padding: 29px 0 0;

            .qodef-comment {
                border-bottom: 1px solid $default-border-color;
                padding-bottom: 32px;

                &:last-child {
                    border-right: none;
                }
            }
        }

        ul.children {
            border-bottom: 1px solid $default-border-color;

            li {
                .qodef-comment {
                    border: none;
                }
            }
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .qodef-comments-title {
        margin-bottom: 44px;

        > * {
            margin: 0;
        }
    }

    .qodef-comment-image {
        position: relative;
        top: 0;
        left: 0;
        width: 82px;
        height: 82px;
        display: block;
        float: left;

        img {
            display: block;
        }
    }

    .qodef-comment-text {
        padding: 0 0 0 106px;
        min-height: 82px;

        .qodef-comment-info {
            @include qodefRelativeHolderLayout();
            margin: 6px 0 3px;
            float: left;
            width: auto;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 15px;
            font-weight: 600;
            color: $default-heading-color;
            position: relative;

            &:after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 1px;
                background-color: $default-heading-color;
                transition: all .2s ease;
            }

            &:hover {
                &:after {
                    width: 40%;
                    transition: all .2s ease;
                }
            }
        }

        .qodef-comment-name {
            float: left;
            margin: 0;
        }

        .qodef-comment-date {
            float: left;
            margin: 0;
            font-size: 15px;
            font-weight: 600;
            color: $default-heading-color;
            width: 100%;
        }

        .qodef-text-holder{
            float: left;
            display: block;
            width: 100%;

            p {
                margin: 7px 0 12px;
            }
        }

        .comment-respond {
            margin: 20px 0;
            clear: both;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 14px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.qodef-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .qodef-comment-form-inner {
        padding: 66px 0 78px;
    }

    .comment-reply-title {
        margin: 0 0 37px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0;
            }
        }

        .qodef-comment-text {

            .qodef-comment-date,
            .qodef-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 9px 0 0 20px;
                display: inline-block;
                line-height: 1em;
            }
        }
    }
}

@include phone-portrait {

    .qodef-comment-holder {

        .qodef-comment-image {
            float: none;
        }

        .qodef-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.qodef-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;

    &::-ms-clear {
        display: none !important; // removes X from input field when typing
    }
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include qodefButtonDefaultStyle();
    @include qodefButtonSize();
    @include qodefButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include qodefButtonSolidHoverColor();
    }
}

.comment-form-cookies-consent {
    margin-bottom: 41px;
}

p.form-submit,
.wpcf7-form p {
    margin: 0;

    .qodef-btn {
        background-color: $additional-background-color;
        color: $default-heading-color;

        &:hover {

        }
    }
}

#respond textarea,
.qodef-style-form textarea,
.wpcf7-form textarea {
    display: block;
    padding-bottom: 53px;
    margin-bottom: 30px;
}

.qodef-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.qodef-newsletter {
    position: relative;

    .wpcf7-form-control.wpcf7-text {
        color: $default-heading-color;
    }

    button.wpcf7-form-control.wpcf7-submit {
        padding: 0;
        background-color: transparent;
        color: $default-heading-color;
        font-family: "ElegantIcons";
        position: absolute;
        right: 0;
        top: 4px;

        &:hover {
            color: $default-text-color !important;
            background-color: transparent !important;
            border-color: transparent !important;
        }

        .qodef-btn-text {
            &:before,
            &:after {
                display: none;
            }
        }
    }

    .ajax-loader {
        display: none !important;
    }
}

.contact-us{
    .wpcf7-form-control.wpcf7-textarea,
    .wpcf7-form-control.wpcf7-text {
        color: #fff;
        border-color: #555;
    }

    .wpcf7-form-control.wpcf7-textarea {
        padding-bottom: 0;
        margin-bottom: 30px;
        max-width: 100%;
    }

    .qodef-btn.qodef-btn-solid {
        color: $default-heading-color;
        background-color: #fff;
        display: block;
        margin: 9px auto;

        &:hover {
            background-color: #fff;
            color: $default-heading-color;
        }
    }
}

.contact-us-2 {
    .wpcf7-form-control.wpcf7-textarea,
    .wpcf7-form-control.wpcf7-text {
        color: $default-text-color;
        border-color: #111;
    }

    .wpcf7-form-control.wpcf7-textarea {
        padding-bottom: 0;
        margin-bottom: 30px;
        max-width: 100%;
    }

    .qodef-btn.qodef-btn-solid {
        float: left;
        background-color: #f8f8f8;
        color: $default-heading-color;

        &:hover {
            background-color: #f8f8f8;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.qodef-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.qodef-slider-is-moving {
        .owl-item {
            .qodef-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.qodef-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                transform: translateY($pos);
            }
        }
    }

    &.qodef-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            transform: translateY(-50%);
            transition: color .2s ease-out;

            &:hover {
                color: $first-main-color;
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 50px;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 5px;

            @include phone-landscape {
                left: 0;
            }
        }

        .owl-next {
            right: 5px;

            @include phone-landscape {
                right: 0;
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 6px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 12px;
                border-radius: 3em;
                background-color: transparent;
                border: 2px solid rgba($default-heading-color, .5);
                box-sizing: border-box;

                transition: background-color .2s ease-out, border .2s ease-out;
            }

            &:hover,
            &.active {

                span {
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .qodef-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .qodef-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                transition: border-color 0.2s ease-out;
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.qodef-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .qodef-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .qodef-wrapper {
        padding: 0 !important;
    }

    .qodef-content {
        height: 100vh;
    }

    &.admin-bar {
        .qodef-content {
            height: calc(100vh - 32px);
        }
    }

    .qodef-content-inner {
        @include qodefTableLayout();

        @include ipad-landscape {
            height: 100vh;
        }
    }

    .qodef-page-not-found {
        @include qodefTableCellLayout();
        padding: 0 20%;
        text-align: center;

        @include phone-landscape {
            padding: 0 14%;
        }

        .qodef-404-title {
            margin: 0;
        }

        .qodef-404-subtitle {
            margin: 12px 0 0;
            position: relative;

            &:after {
                content: '';
                width: 25px;
                height: 2px;
                position: absolute;
                bottom: -9px;
                left: 50%;
                transform: translateX(-50%);
                background-color: #fff;
            }
        }

        .qodef-404-text {
            margin: 32px auto 0;
            font-size: 18px;
            max-width: 500px;
        }

        .qodef-searchform {
            max-width: 500px;
            margin: 20px auto;

            .input-holder {
                border-bottom: 1px solid #fff;

                input,
                button {
                    background-color: transparent;
                    border: 0;
                    border-radius: 0;
                    box-sizing: border-box;
                    color: #fff;
                    display: inline-block;
                    float: left;
                    font-family: $default-heading-font;
                    font-size: 15px;
                    font-weight: inherit;
                    height: 50px;
                    line-height: 48px;
                    outline: 0;
                    position: relative;
                    vertical-align: top;
                    -webkit-appearance: none;
                    transition: color .2s ease-in-out;
                }

                input.search-field {
                    padding: 0;
                    width: calc(100% - 50px);

                    @include qodefPlaceholder {
                        color: #fff;
                    }
                }

                button {
                    background: transparent;
                    color: #fff;
                    cursor: pointer;
                    font-size: 17px;
                    width: 50px;
                    transition: color .2s ease-in-out;
                    padding: 0;

                    &:hover {
                        color: $default-text-color;
                    }

                    span {
                        display: block;
                        right: 0;
                        line-height: inherit;
                        position: absolute;
                        top: 0;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }
        }
    }

    @include ipad-landscape {
        .qodef-content {
            height: auto;
            min-height: 100vh;
            margin-top: -90px!important;

            .qodef-content-inner {
                min-height: 100vh;
            }
        }

        &.admin-bar {
            .qodef-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#qodef-back-to-top {
    position: fixed;
    width: 47px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        transition: opacity 0.15s ease, visibility .0s .15s;
    }

    &.on {
        opacity: 1;
        visibility: visible;

        transition: opacity 0.3s ease;
    }

    > span {
        position: relative;
        display: block;
        width: 45px;
        height: 45px;
        line-height: 46px;
        font-size: 15px;
        letter-spacing: 0;
        font-weight: 600;
        text-align: center;
        color: $default-heading-color;
        background-color: $additional-background-color;
        border: 1px solid $additional-background-color;
        overflow: hidden;

        transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;

        span {
            position: relative;

            &:before {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                height: 1px;
                background-color: currentColor;
                width: 100%;
                -webkit-transform-origin: 100% 50%;
                -moz-transform-origin: 100% 50%;
                transform-origin: 100% 50%;
                transition: transform .4s cubic-bezier(.28,.38,0,.81);
            }

            &:after {
                content: '';
                position: absolute;
                left: 0;
                bottom: 0;
                height: 1px;
                background-color: currentColor;
                width: 100%;
                transform: scaleX(0);
                -webkit-transform-origin: 0 50%;
                -moz-transform-origin: 0 50%;
                transform-origin: 0 50%;
                transition: transform .4s cubic-bezier(.28,.38,0,.81) .2s;
            }
        }

        &:hover {

            span {
                &:before {
                    transform: scaleX(0);
                }
                &:after {
                    transform: scaleX(1);
                }
            }
        }
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.qodef-self-hosted-video-holder {
    .qodef-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

.qodef-underline {
    position: relative;
    width: 100%;

    &:before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 1px;
        background-color: currentColor;
        width: 100%;
        -webkit-transform-origin: 100% 50%;
        -moz-transform-origin: 100% 50%;
        transform-origin: 100% 50%;
        transition: transform .4s cubic-bezier(.28,.38,0,.81);
    }

    &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        height: 1px;
        background-color: currentColor;
        width: 100%;
        transform: scaleX(0);
        -webkit-transform-origin: 0 50%;
        -moz-transform-origin: 0 50%;
        transform-origin: 0 50%;
        transition: transform .4s cubic-bezier(.28,.38,0,.81) .2s;
    }

    &:hover {
        &:before {
            transform: scaleX(0);
        }
        &:after {
            transform: scaleX(1);
        }
    }
}

.vc_row.vc_row-o-full-height {
    height: 100vh;
}