/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    &:first-of-type {
        .qodef-widget-title-holder {
            .qodef-widget-title {
                margin-top: 0;
            }
        }
    }

    .qodef-widget-title-holder {
        margin-bottom: $widget-title-margin;

        .qodef-widget-title {
            color: $widget-heading-color;
            margin: 10px 0 3px;
        }

        .qodef-separator {
            border-bottom-width: 3px;
            border-color: $widget-text-color;
            width: 30px;
        }
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;
            color: $widget-link-color;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            padding: 9px 0 4px 8px;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and animation on link hover

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {
        .qodef-widget-title {
            margin-bottom: 0;
        }

        ul {
            li {
                overflow: hidden;

                a {
                    display: inline-block;
                    vertical-align: middle;
                    overflow: hidden;
                    padding: 0 15px 0 0;
                    position: relative;
                    transition: all .2s ease;
                    font-weight: 400;
                    width: auto;

                    &:last-of-type {
                        padding: 0;
                    }

                    &:before {
                        content: '';
                        position: absolute;
                        bottom: 4px;
                        left: 0;
                        height: 1px;
                        background-color: currentColor;
                        width: 100%;
                        transform: scaleX(0);
                        -webkit-transform-origin: 100% 50%;
                        -moz-transform-origin: 100% 50%;
                        transform-origin: 100% 50%;
                        transition: transform .4s cubic-bezier(.28,.38,0,.81);
                    }

                    &:hover {
                        color: $widget-link-color;
                        &:before {
                            transform: scaleX(1);
                            -webkit-transform-origin: 0% 50%;
                            -moz-transform-origin: 0% 50%;
                            transform-origin: 0% 50%;
                        }
                    }
                }
            }
        }
    }

    &.widget_nav_menu {
        ul {
            li {
                padding: 5px 0;

                &:first-child {
                    padding-top: 0;
                }

                a {
                    font-weight: 600;
                    padding: 0;
                    line-height: 24px;
                    font-size: 16px;

                    &:before {
                        content: '';
                        position: absolute;
                        bottom: 4px;
                        left: 0;
                        height: 1px;
                        background-color: currentColor;
                        width: 100%;
                        transform: scaleX(0);
                        -webkit-transform-origin: 100% 50%;
                        -moz-transform-origin: 100% 50%;
                        transform-origin: 100% 50%;
                        transition: transform .4s cubic-bezier(.28,.38,0,.81);
                    }

                    &:hover {
                        color: $widget-link-color;
                        &:before {
                            transform: scaleX(1);
                            -webkit-transform-origin: 0% 50%;
                            -moz-transform-origin: 0% 50%;
                            transform-origin: 0% 50%;
                        }
                    }
                }
            }

            &#menu-soc-menu {
                li {
                    a {
                        &:before {
                            content: '';
                            position: absolute;
                            bottom: 4px;
                            left: 0;
                            height: 1px;
                            background-color: currentColor;
                            width: 100%;
                            transform: scaleX(1);
                            -webkit-transform-origin: 100% 50%;
                            -moz-transform-origin: 100% 50%;
                            transform-origin: 100% 50%;
                            transition: transform .4s cubic-bezier(.28,.38,0,.81);
                        }

                        &:after {
                            content: '';
                            position: absolute;
                            left: 0;
                            bottom: 4px;
                            height: 1px;
                            background-color: currentColor;
                            width: 100%;
                            transform: scaleX(0);
                            -webkit-transform-origin: 0 50%;
                            -moz-transform-origin: 0 50%;
                            transform-origin: 0 50%;
                            transition: transform .4s cubic-bezier(.28,.38,0,.81) .2s;
                        }

                        &:hover {
                            color: $widget-link-color;
                            &:before {
                                transform: scaleX(0);
                            }
                            &:after {
                                transform: scaleX(1);
                            }
                        }
                    }
                }
            }
        }
    }
	
	&.widget_archive,
	&.widget_categories {
		ul li a {
			padding-right: 0;
		}
	}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and w/o animation on link hover

    &.widget_recent_comments,
    &.widget_rss {
        .qodef-widget-title {
            margin-bottom: 0;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            background-color: $default-text-color;
            color: $widget-text-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    .qodef-searchform {
        .input-holder {
            border-bottom: 1px solid $default-heading-color;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-heading-font;
                font-size: 15px;
                font-weight: inherit;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;
                transition: color .2s ease-in-out;
            }

            input.search-field {
                padding: 0;
                width: calc(100% - #{$widget-search-form-height});

                @include qodefPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                background: $widget-list-item-background-color;
                color: $default-heading-color;
                cursor: pointer;
                font-size: 17px;
                width: $widget-search-form-height;
                transition: color .2s ease-in-out;
                padding: 0;

                &:hover {
                    color: $default-text-color;
                }

                span {
                    display: block;
                    right: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    //&.widget_archive, // to affect dropdown in woo widgets
    //&.widget_categories,
    //&.widget_text {
        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0;
            position: relative;
            vertical-align: top;
            width: 100%;
            transition: color .2s ease-in-out;
        }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    .tagcloud {
        padding-top: 4px;

        a {
            border-radius: 2px;
            color: $default-heading-color;
            display: inline-block;
            font-family: $default-heading-font;
            font-size: 15px !important;
            font-weight: 700;
            line-height: 1em;
            margin: 0 10px 8px 0;
            padding: 0;
            text-transform: lowercase;
            position: relative;

            &:before {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                height: 1px;
                background-color: currentColor;
                width: 100%;
                transform: scaleX(0);
                -webkit-transform-origin: 100% 50%;
                -moz-transform-origin: 100% 50%;
                transform-origin: 100% 50%;
                transition: transform .4s cubic-bezier(.28,.38,0,.81);
            }

            &:hover {
                color: $default-heading-color;
                &:before {
                    transform: scaleX(1);
                    -webkit-transform-origin: 0% 50%;
                    -moz-transform-origin: 0% 50%;
                    transform-origin: 0% 50%;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
                padding-top: $widget-list-item-padding;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}